body {
    background-color: #f5f5f5;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.wrapper {
    max-width: 1600px;
    margin: 0 auto;
}

.wrapper-nav {
    margin: 0 auto;
    width: 1530px;
    display: flex;
    justify-content: space-between;
}

.wrapper-nav img {
    padding-left: 5px;
}

.wrapper-user {
    margin: 0 auto;
    width: 1500px;
}

.arrowButton {
    border: none;
    background: white;
    color: #757575;
}

.home-icon {
    margin-right: 5px;
}

a {
    text-decoration: none;
}

table {
    table-layout: fixed;
    width: 100%;
}

.profile-photo_big {
    cursor: pointer;
    width: 200px;
    height: 200px;
    margin-top: 15px;
    border-radius: 100%;
    margin-top: 30px;
}

.linkPadding {
    cursor: pointer;
    display: flex;
    width: 23vw;
    margin: 10px auto;
    margin-top: 10vw;
}

.linkPadding h5 {
    padding: 10px 15px;
    color: rgba(0, 0, 0, 0.54);
    font-size: 20px;
    line-height: 24px;
}

.photo-small {
    width: 48px;
    height: 48px;
    margin-right: 15px;
    border-radius: 100%;
}

.photo-modal {
    width: 666px;
    height: 666px;
    border-radius: 100%;
}

.flex {
    display: flex;
}

.tableInput {
    text-align: center;
    border: 0;
    width: 100%;
    position: relative;
}

.search-icon {
    position: absolute;
    top: 11px;
    right: 10px;
}

input {
    padding: 10px;
    font-family: FontAwesome, "Open Sans", Verdana, sans-serif;
    font-style: normal;
    font-weight: normal;
    text-decoration: inherit;
}

.tableInput:last-child {
    max-width: 100px;
}

rect {
    fill: rgb(255, 255, 255);
    stroke: rgb(0, 0, 0);
    stroke-width: 1px;
}

.room:hover,
.room.active {
    fill: rgb(247, 218, 124);
    stroke: rgb(0, 0, 0);
    stroke-width: 1px;
    cursor: pointer;
}

.selectedRoom {
    fill: rgb(175, 248, 161);
    cursor: pointer;
}

text {
    pointer-events: none;
}

tr th,
tr td {
    width: 16.76%;
    text-align: center;
    padding: 4px 0 4px 10px !important;
}

.map {
    max-width: 800px;
    margin: 0 auto;
}

.tableInput:focus,
button:focus {
    outline: none;
}

.filterPanel {
    flex-direction: column;
    width: 253px;
    height: auto;
    background-color: white;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
    border-radius: 7px;
    margin-top: 24px;
    min-height: 40vw;
    margin-bottom: 25px;
}

.filterPanel div {
    box-shadow: none;
}

.filterButtons button {
    width: 75px;
    height: 32px;
    background-color: white;
    color: #57B12E;
    border-color: #57B12E;
    border-style: solid;
    cursor: pointer;
}

.filterButtons p {
    color: rgba(0, 0, 0, 0.54);
    padding: 0;
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    line-height: 1;
    margin-left: 10px;
}

.filterButtons .active {
    color: white;
    background-color: #57B12E;
}

.filterButtons {
    margin-left: 15px;
}

.optionOne {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-left-width: 1px;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-right-width: 0;
}

.optionTwo {
    border-width: 1px;
}

.optionThree {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-left-width: 0;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-right-width: 1px;
}
